import MyPagination from "../../../components/MyPagination";
import mixins from "../../../utils/mixins";
import {common} from "../../../utils/api/url";
import {levelText, MessageError, MessageSuccess, MessageWarning} from "../../../utils/utils";
import LayoutTb from "../../../components/Layout/LayoutTb";
import MySelect from "../../../components/MySelect";
import MyInputButton from "../../../components/search/MyInputButton";
import DialogNormal from "../../../components/utils/dialog/DialogNormal";
import DrawerLayoutRight from "../../../components/utils/dialog/DrawerLayoutRight";
import padWoValidater from "../../../utils/validater/padWoValidater";
import MyDoorExceptionStatus from "../../../components/utils/status/MyDoorExceptionStatus";
import MyDatePicker from "../../../components/MyDatePicker";
export default {
  mixins: [mixins, padWoValidater],
  components: {MyPagination,LayoutTb,MySelect,MyInputButton,DialogNormal,DrawerLayoutRight,MyDoorExceptionStatus,MyDatePicker},
  data(){
    return {
      tableData: [],
      searchDate: [],
      typeList: [],
      searchKey: '',
      visible: false,
      modalVisible: false,
      modalConfVisible: false,
      dialogLoading: false,
      drawerLoading: false,
      visibleConfim: false,
      drawerVisible: false,
      loading: false,
      perLoading: false,
      picLoading: false,
      mutiDeleteLoading: false,
      mutiUnbindLoading: false,
      mutiResetLoading: false,
      searchOnline: '',
      config: {},
      subTitle: '',
      deleteSetTitle: '确认需要解除该限制吗？',
      status: '',
      deviceList: [],
      id: '',
      filterDoorExceptionStatusText: ''
    }
  },
  created() {
    this.init();
  },
  methods: {
    init(){
      let params = {
        page: this.page,
        num: this.num,
        searchKey: this.searchKey,
        status: this.status,
        beginTime: this.searchDate && this.searchDate.length > 0 ? this.searchDate[0] : '',
        endTime: this.searchDate && this.searchDate.length > 0 ? this.searchDate[1] : '',
      };
      params = this.$qs.stringify(params);
      this.$axios.post(common.dormaccess_exception_page, params).then(res => {
        if (res.data.data){
          this.tableData = res.data.data.list;
          this.total = res.data.data.totalCount;
          this.num = res.data.data.num;
          this.page = res.data.data.currentPage;
        }
      });
    },
    unbindMutiInfo(){
      let arr = [];
      for (let i = 0; i < this.deviceList.length; i++){
        arr.push(this.deviceList[i].id);
      }
      let params = {
        ids: arr.join()
      };
      params = this.$qs.stringify(params);
      this.mutiUnbindLoading = true;
      this.$axios.post(common.dormaccess_exception_unbind, params).then(res => {
        if (res.data.code == 200){
          this.init();
          this.deviceList = [];
          this.$refs.refTable.clearSelection();
          MessageSuccess(res.data.desc);
        }else {
          MessageError(res.data.desc);
        }
        this.mutiUnbindLoading = false;
      });
    },
    sizeChange(event){
      this.page = 1;
      this.num = event;
      this.init();
    },
    currentPage(event){
      this.page = event;
      this.init();
    },
    jumpPage(data){
      this.page = data;
      this.init();
    },
    search(data){
      this.page = 1;
      this.searchKey = data.input;
      this.init();
    },
    handleSelectionChange(data){
      this.deviceList = data;
    },
    handleTime(data){
      this.searchDate = data;
    },
    getRowKeys(row) {
      return row.id
    },
    unbindInfo(row){
      if (row.status == 0){
        this.deleteSetTitle = this.$t("确认需要解除该异常吗？");
      }else if (row.status == 1){
        this.deleteSetTitle = this.$t("确认需要解除该限制吗？");
      }
      this.id =  row.id;
      this.subTitle = row.name;
      this.visibleConfim = true;
    },
    handleOkChange(data) {
      this.dialogLoading = true;
      let url = "";
      let params = {
        ids: this.id
      };
      url = common.dormaccess_exception_unbind;
      params = this.$qs.stringify(params);
      this.$axios.post(url, params).then(res => {
        if (res.data.code == 200){
          this.init();
          MessageSuccess(res.data.desc);
        }else {
          MessageError(res.data.desc);
        }
        this.visibleConfim = false;
        this.dialogLoading = false;
      });
    },
    handleCancelChange(data) {
      this.visibleConfim = false;
    },
    closeDialog(event){
      this.id = "";
    },
    fliterTable(value, row, column){
      for (let item in value){
        if (item == 'status'){
          this.filterDoorExceptionStatusText = '';
          this.status = value[item][0];
          for (let i = 0; i < this.filterDoorExceptionStatus.length; i++){
            if (this.status == this.filterDoorExceptionStatus[i].value){
              this.filterDoorExceptionStatusText = this.filterDoorExceptionStatus[i].text;
            }
          }
        }
      }
      this.page = 1;
      this.init();
    },
    exportInfo(){
      let url = "";
      let params = {
        page: 1,
        num: 99999,
        searchKey: this.searchKey,
        status: this.status,
        beginTime: this.searchDate && this.searchDate.length > 0 ? this.searchDate[0] : '',
        endTime: this.searchDate && this.searchDate.length > 0 ? this.searchDate[1] : '',
      };
      params = this.$qs.stringify(params);
      url = common.dormaccess_exception_export;
      window.open(url+"?"+params, "_self");
    }
  }
}
